footer {
  position: relative;
  bottom: 0;
  padding-bottom: 10px;
  text-align: center;
  width: 100%;
  background-color: #9e2aeb;
  color: #fff;
}

.footer-con {
  width: 80%;
  margin: auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.servises ul,
.social ul {
  display: flex;
  justify-content: start;
  flex-direction: column;
  align-items: start;
  margin: 50px auto;
  text-align: initial;
  line-height: 2;
}

.servises ul li:hover,
.social ul li:hover {
  background-color: #8411d1;
}
